<template>
    <li class="catalog__item">
        <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
            <img :src="product.image" :alt="product.title">
        </router-link>
        
        <h3 class="catalog__title">
            <a href="#">
                {{ product.title }}
            </a>
        </h3>
        
        <span class="catalog__price">
            {{ product.price | numberFormat }}
        </span>
        
        <ul class="colors colors--black">
            <li class="colors__item" v-for="color of product.colors" :key="color.id">
                <label class="colors__label" :for="product.id + color.id">
                    <input :id="product.id + color.id" class="colors__radio sr-only" type="radio" :name="product.id" :value="color.code">
                    <span class="colors__value" :style="background(color.code)"></span>
                </label>
            </li>
        </ul>
    </li>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";

export default {
    name: 'ProductItem',
    props: {
        product: {
            type: Object,
            default() {
                return{}
            }
        }
    },

    data() {
        return {
            color: '#73B6EA'
        }
    },

    filters: {
        numberFormat
    },
    
    methods: {
        background(colvalue) {
            return {'background-color': colvalue,};
        },
        gotoPage
    }
}
</script>