import BaseFormFild from '@/components/BaseFormFild';

export default {
    name: 'BaseFormText',
    props: ['title', 'error', 'placeholder', 'value'],
    components: {BaseFormFild},
    computed: {
        dataValue: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit('input', value);
            }
        }

    }
}